.page-title {
  background: #1193F5;
  height: 2.4rem;
  line-height: 2.4rem;
  .title {
    color: #FFFFFF;
    font-size: 1.2rem;
  }
  .icon-me, .icon-app {
    color: #ffffff;
    font-size: 1.7rem;
    text-decoration: none;
  }
  .icon-left {
    color: #ffffff;
    font-size: 1.5rem;
    text-decoration: none;
    margin-left: -0.3rem;
  }
}

.video-card {
  background: #000000;
  height: auto;
  overflow: hidden;
  //  padding-bottom: 44px;
}

.video-title {
  font-size: 0.6rem;
  color: #333333;
  .card-content-inner {
    padding: 0.3rem 0.5rem;
  }
}

.tag-item {
  background: #d7d7d7;
  font-size: 0.8rem;
  color: #333;
  border-radius: 2px;
  height: 1.2rem;
  line-height: 1.2rem;
  padding: 0 0.2rem;
  margin-right: 0.5rem;
  cursor: pointer;
  display: inline-block;
  margin-bottom: 0.4rem;
}

.card-bottom {
  width: 100%;
  display: inline-block;
  .tags {
    width: 70%;
    height: 1.6rem;
    display: inline-block;
    overflow: hidden;
  }
  a.link {
    display: inline-block;
    float: right;
    span.icon {
      font-size: 1.2rem;
      margin-left: 0.5rem;
    }
    span.fa {
      font-size: 1.2rem;
      height: 2.2rem;
      line-height: 2.2rem;
      margin-left: 0.5rem;
    }
  }
}

.card-items-block {
  padding-left: 0;
  padding-right: 0;
  .card-items {
    .card-detail {
      color: #666;
      font-size: 1rem;
    }
  }
}

.login-user {
  width: 80%;
  margin: 0 auto;
  img {
    width: 4.8rem;
    height: 4.8rem;
    margin: 0 auto;
    border-radius: 50%;
    display: block;
  }
  b {
    color: #ffffff;
    width: 100%;
    display: block;
    height: 2rem;
    line-height: 2rem;
    font-size: 1rem;
    text-align: center;
  }
}

.side-bar-link {
  width: 100%;
  margin-top: 4rem;
  list-style: none;
  li {
    width: 90%;
    border-bottom: 1px solid #f5f5f5;
    height: 2.4rem;
    line-height: 2.4rem;
    text-align: center;
    a {
      color: #f9f9f9;
      text-decoration: none;
    }
  }
}

.oauth2-login {
  width: 80%;
  margin: 4rem auto;
  span {
    width: 68%;
    margin: 2rem auto;
    height: 2.4rem;
    line-height: 2.4rem;
    border-bottom: 1px solid #cccccc;
    text-align: center;
    display: block;
  }
  .ds-login {
    margin: 0 auto;
  }
}

.tag-items {
  width: 100%;
  margin: 0rem auto;
  padding-left: 15px;
  height: auto;
  background: #ffffff;
  li {
    display: inline-block;
    margin-top: 0.4rem;
    .tag-item {
        height:1.6rem;
      line-height:1.6rem;
      padding:0 0.4rem;
    }
  }
}